import React, { Component } from "react";
import { Input } from "antd";
const Search = Input.Search;
import { get, post } from "util/fetch";
import { message, Button } from "antd";
import "./index.less";
const routePath = `/api/english/keke`;
let number = 1;
const kekePath =
  "http://word.kekenet.com/index.php?do=recall&type=meaning&cid=120&coid=";
export default class index extends Component {
  state = {
    kekeLink: `${kekePath}${number}`,
    wordList: [],
    collection_name: "高考英语大纲词汇",
    collection_level: `第${number}关`,
    isReflesh: false
  };
  handleKekeLink = value => {
    console.log(value);
    let params = {
      url: value
    };
    post("/api/common/keke", params).then(res => {
      this.setState({
        wordList: res.data.wordList,
        isReflesh: true
      });
      log(res);
    });
  };
  handleSeachChange = (e, key) => {
    log(e.target, key);

    number = e.target.value;
    this.setState({
      isReflesh: false,
      collection_level: `第${number}关`,
      kekeLink: `${kekePath}${number}`
    });
  };
  addBatch = async () => {
    let length = this.state.wordList.length;
    let { collection_name, collection_level } = this.state;
    log({ collection_name, collection_level });
    // return;
    for (let i = 0; i < length; i++) {
      let item = this.state.wordList[i];

      item = Object.assign({}, item, { collection_name, collection_level });

      let result = await post(routePath, item);
    }
    message.success("批量添加成功", 3);
  };
  handleItem = item => {
    let { collection_name, collection_level } = this.state;
    item = Object.assign({}, item, { collection_name, collection_level });
    post(routePath, item)
      .then(res => {
        if (res.returnCode === 0) {
          message.success("添加成功");
        } else {
          message.success("添加失败");
        }
        log(res);
      })
      .catch(err => {
        message.success("添加失败");
      });
  };
  render() {
    const inputCell = (
      <div
        className="input-cell"
        style={{ marginTop: 10, backgroundColor: "#fff" }}
      >
        <h3 style={{ borderBottom: "1px solid #ddd", paddingLeft: 20 }}>
          输入参数
        </h3>
        <div className="content">
          <span>可可链接：</span>
          <Search
            placeholder="输入可可链接"
            enterButton="Search"
            // size="large"
            style={{ width: 650 }}
            value={this.state.kekeLink}
            // onChange={e => this.handleSeachChange(e, "kekeLink")}
            onSearch={value => this.handleKekeLink(value)}
          />
          <div>
            <span>词库名称：</span>
            <Input
              value={this.state.collection_name}
              style={{ width: 500 }}
              onChange={e => this.handleSeachChange(e, "collection_name")}
            />
          </div>
          <div>
            <span>词库关数：</span>
            <Input
              value={number}
              style={{ width: 500 }}
              type="number"
              onChange={e => this.handleSeachChange(e, "number")}
            />
            {this.state.collection_level}
            <Button
              type="primary"
              disabled={!this.state.isReflesh}
              onClick={this.addBatch}
            >
              批量添加
            </Button>
          </div>
        </div>
      </div>
    );
    const content = (
      <div className="table">
        <div className="row-line item">
          <div className="w10">序号</div>
          <div className="w10">单词</div>
          <div className="w10">发音</div>
          <div className="cell">音标</div>
          <div className="cell">定义</div>
        </div>
        {this.state.wordList.map((item, index) => (
          <div
            key={JSON.stringify(index)}
            className="item"
            onClick={e => this.handleItem(item)}
          >
            <div className="w10">{index + 1}</div>
            <div className="w10">{item.word}</div>
            <div className="w10">
              <img
                style={{ width: 30, cursor: "pointer" }}
                src="/images/sound.png"
                title={item.pronunciation_url}
                onClick={() => {
                  let phonetic_sound = new Audio(item.pronunciation_url);
                  phonetic_sound.play();
                }}
              />
            </div>
            <div className="cell">{item.phonetic_symbol}</div>
            <div className="cell">{item.definition}</div>
          </div>
        ))}
      </div>
    );
    return (
      <div className="keke-demo">
        {inputCell}
        {content}
      </div>
    );
  }
}
